.about-mf {
  padding: 100px 20px;
  background-color: #f9f9f9;
}

.about-content {
  display: flex;
  align-items: flex-start;
}

.about-img {
  flex: 0 0 auto;
  margin-right: 20px;
}

.about-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
}

.title-and-links{
  display: flex;
  flex-direction: column;
  padding-top: 60px;
  align-items: center;
}

.cv-links {
  display: flex;
  flex-direction: row;
}

.about-info p {
  color: #666;
}

.about-info-item {
  margin: 10px;
}

.title-s {
  font-weight: bold;
  color: #333;
}

.about-me .title-left {
  font-size: 1.5rem;
  font-weight: bold;
  color: #1e1e1e;
}

.lead {
  font-size: 1rem;
  color: #666;
  text-align: justify;
}


.info-container {
  display: flex;
  flex-direction: column;
  margin-left: 0;
}

.cv-download-link {
  display: flex;
  align-items: center;
  margin-left: 20px;
  border: 1px solid transparent;
}

.cv-download-icon {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  height: auto;
  cursor: pointer;
}

.cv-download-icon:hover {
  transform: scale(1.1); 
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  filter: brightness(1.2);
}

@media (max-width: 768px) {
.about-mf {
  padding: 60px 20px;
}
}